:root {
  --blue-color: #00acdc;
}

html, body {
  font-family: Roboto, sans-serif;
  font-weight: 500px;
  margin: 0;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo_link {
  margin: 0;
  padding: 0;
  line-height: 0;
}

.logo_img {
  width: 40px;
  height: 40px;
  padding: 20px 0;
}

.nav {
  height: 60px;
  line-height: 60px;
}

.nav_list {
  margin: 0;
  padding: 0;
}

.nav_item {
  list-style: none;
  display: inline-block;
}

.nav_item a {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

a.is-active {
  color: var(--blue-color);
}

.nav_item a.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

a[data-qa="hover"] {
  color: var(--blue-color);
}

a:hover {
  color: var(--blue-color);
  border-bottom: 2px solid var(--blue-color);
  text-decoration: none;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

